@import '../../style/mixins';
@import '../../style/colors';

ul {
    list-style-type: none;
    margin: 0;
    padding: 0;

    li {
        padding: 10px;

        a {
            svg {
                path {
                    fill: $text-2;
                }

                &:hover {
                    path {
                        fill: $text-1;
                    }
                }
            }
        }
    }
    
    &.vectical {
        @include vertical-layout(center, flex-start);
    }

    &.horizontal {
        @include horizontal-layout(flex-start, center);
    }
}