@import '../../style/colors';
@import '../../style/mixins';

nav {
	@include horizontal-layout(center, center);
	position: fixed;
	z-index: 9999;
	top: 0;
	left: 0;
	width: 100%;
	display: flex;
	overflow: hidden;
	background-color: $primary-2;

	.inner {
		@include inner;

		#name {
			float: left;
			color: $text-1;
			font-size: 22px;
			font-weight: 700;
			letter-spacing: 2px;

			@include md {
				font-size: 26px;
			}
		}

		#menu-button {
			float: right;
			width: 30px;

			&::before,
			div,
			&::after {
				float: right;
				background-color: $primary-3;
				border-radius: 3px;
				content: "";
				display: block;
				height: 3px;
				margin: 3px 0;
				transition: all 0.15s ease-in-out;
			}

			@include lg {
				display: none;
			}

			&::before {
				width: 80%;
			}

			div {
				width: 100%;
			}

			&::after {
				width: 60%;
			}
		}

		#menu {
			display: none;
			width: 100%;
			padding: 30px 0;
			float: left;

			@include lg {
				@include horizontal-layout(initial, initial);
				width: unset;
				padding: 0;
				float: right;
			}

			a {
				color: $text-2;
				font-size: 14px;
				font-weight: 600;
				margin: 10px 0 10px;
				padding: 5px 15px;
				border-radius: 6px;

				&:hover {
					color: $text-1;
				}

				&:last-child {
					border: 2px solid $text-2;
					box-sizing: border-box;

					&:hover{
						@include lg {
							color: $primary-2;
							background-color: $text-2;
						}
					}
				}
			}
		}
	}

	&.show-menu {
		height: 100vh;

		.inner {
			height: 100%;

			#menu-button {
				&::before {
					width: 100%;
					transform: translateY(9px) rotate(45deg) !important;
				}
	
				div {
					transform: scale(0) !important;
				}
	
				&::after {
					width: 100%;
					transform: translateY(-9px) rotate(-45deg) !important;
				}
			}
			
			#menu {
				@include vertical-layout(center, initial);
			}
		}
	}
}
