@import './breakpoints';

@mixin sm {
	@media (min-width: #{$screen-sm-min}) {
		@content;
	}
}

@mixin md {
	@media (min-width: #{$screen-md-min}) {
		@content;
	}
}

@mixin lg {
	@media (min-width: #{$screen-lg-min}) {
		@content;
	}
}

@mixin xl {
	@media (min-width: #{$screen-xl-min}) {
		@content;
	}
}

@mixin layout($direction, $v-align, $h-align) {
	display: flex;
	flex-direction: $direction;
	align-items: $v-align;
	justify-content: $h-align;
}

@mixin vertical-layout($v-align, $h-align) {
	@include layout(column, $v-align, $h-align);
}

@mixin horizontal-layout($v-align, $h-align) {
	@include layout(row, $v-align, $h-align);
}

@mixin horizontal-float-right($v-align, $h-align) {
	@include layout(row-reverse, $v-align, $h-align)
}

@mixin centered-layout {
	@include layout(column, center, center);
}

@mixin inner {
	width: 100%;
	padding: 15px;
	box-sizing: border-box;
	overflow: hidden;

	@include sm {
		padding: 20px;
	}

	@include md {
		padding: 25px;
		max-width: 720px;
	}

	@include lg {
		padding: 25px 30px;
		max-width: 1200px;
	}
}

