@import '../../style/colors';
@import '../../style/mixins';

.heading {
    @include vertical-layout(center, center);
    padding-top: 15px;
    padding-bottom: 200px;

    h1 {
        font-size: 24px;
        padding-bottom: 5px;
        color: $text-1;
    }
    
    .top {
        width: 50px;
        border-bottom: 2px solid $text-1;
    }

    .bottom {
        width: 200px;
        border-bottom: 1px solid $text-2l;
    }
}