@import '../../style/mixins';
@import '../../style/colors';

#home {
    @include horizontal-layout(center, center);
    background-color: $primary-2;
	padding-top: 59.6px;
	
	@include sm {
		padding-top: 69.6px;;
	}

	@include md {
		padding-top: 85.6px;
	}
    
    .inner {
        @include inner;
        
        @include md {
            @include horizontal-float-right(stretch, center);
        }

        .image {
            @include vertical-layout(center, center);

            img {
                width: 95%;
            }
    
            @include md {
                width: calc(5/12*100%);
                float: right;
            }
        }

        .content {
            @include vertical-layout(flex-start, center);
            padding: 50px 0;

            @include md {
                width: calc(7/12*100%);
                float: right;
            }

            p {
                font-size: 16px;
                color: $text-2;

                &#occupation {
                    margin: 15px 0;
                    padding: 10px 20px;
                    padding-left: 0;
                    border-top: 1px solid $text-2l;
                    border-bottom: 1px solid $text-2l;
                }

                @include md {
                    font-size: 18px;
                }

                @include lg {
                    font-size: 21px;
                }
            }

            h1 {
                font-size: 26px;
                color: $text-1;

                @include md {
                    font-size: 30px;
                }

                @include lg {
                    font-size: 35px;
                }
            }

            #portfolio-ln {
                font-size: 13px;
                font-weight: 600;;
                margin-top: 10px;
                padding: 8px 20px;
                border: 2px solid $text-1;
                border-radius: 20px;

                svg {
                    margin-left: 10px;
                }

                &:hover {
                    color: $text-3;
                    background-color: $primary-3;

                    svg {
                        path {
                            fill: $text-3;
                        }
                    }
                }

                @include md {
                    font-size: 15px;
                }

                @include lg {
                    font-size: 18px;
                }
            }
        }
    }
}

